import {uniCalendar} from "@/components/uni-calendar/uni-calendar"
import {formatDate} from '@/utils/utils.js';
import uniRequest from '@/utils/uni-request';
export default {
	components: {
	        uniCalendar
	    },
	data() {
		return {
			priArray: [],
			priIndex: 0,
			startTime:formatDate(new Date()),
			endTime:formatDate(new Date()),
			timeType:'',
			statusArray:[{
				name:'新',
				enName:'new'
			},{
				name:'需求明确中',
				enName:'explicit'
			},{
				name:'实现中',
				enName:'doing'
			},{
				name:'已完成',
				enName:'finish'
			}],
			statusIndex:0
		}
	},
	onShow() {
		// 获取优先级
		uniRequest.get('/priority/get').then((res)=>{
			this.priArray=res.data.result;
		})
		// 获取状态
	},
	methods: {
		 bindTextAreaBlur(e) {
		            console.log(e.detail.value)
		        },
		bindPickerChange(e) {
		            console.log('picker发送选择改变，携带值为', e.target.value)
		            this.statusIndex = e.target.value
		        },	
		open(type){
		            this.$refs.calendar.open();
					this.timeType=type;
		        },
		confirm(e) {
		            if(this.timeType==='start'){
						this.startTime=e.fulldate;
					}
					if(this.timeType==='end'){
						this.endTime=e.fulldate;
					}
		        }
	}
}
